//  
import React, { Component } from 'react';
import ProgressChart from './ProgressChart';

class Statistics extends Component {
    constructor(props) {
        super(props);
        this.state = {
            stats: null,
            weeklyData: [],
            loading: true,
            error: ''
        };
    }

    componentDidMount() {
        this.loadStats();
        this.loadWeeklyData();
    }

    loadStats = async () => {
        try {
            const response = await fetch('/api/stats', {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('authToken')}`
                }
            });

            if (!response.ok) throw new Error('Failed to load stats');

            const data = await response.json();
            this.setState({ stats: data, loading: false });
        } catch (err) {
            this.setState({ error: err.message, loading: false });
        }
    };

    loadWeeklyData = async () => {
        try {
            const response = await fetch('/api/stats/weekly', {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('authToken')}`
                }
            });

            if (!response.ok) throw new Error('Failed to load weekly data');

            const data = await response.json();
            this.setState({ weeklyData: data.weeklyData });
        } catch (err) {
            console.error('   :', err);
        }
    };

    render() {
        const { stats, weeklyData, loading, error } = this.state;

        if (loading) return <div> ...</div>;
        if (error) return <div>: {error}</div>;
        if (!stats) return null;

        return (
            <div style={{ padding: '20px', backgroundColor: '#f8f9fa', borderRadius: '8px', marginBottom: '20px' }}>
                <h3 style={{ marginBottom: '15px' }}><span role="img" aria-label=""></span>  </h3>
                <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(150px, 1fr))', gap: '15px' }}>
                    <div style={{ padding: '15px', backgroundColor: '#fff', borderRadius: '4px', textAlign: 'center' }}>
                        <div style={{ fontSize: '24px', fontWeight: 'bold', color: '#007bff' }}>{stats.deckCount}</div>
                        <div style={{ fontSize: '14px', color: '#6c757d', marginTop: '5px' }}> </div>
                    </div>
                    <div style={{ padding: '15px', backgroundColor: '#fff', borderRadius: '4px', textAlign: 'center' }}>
                        <div style={{ fontSize: '24px', fontWeight: 'bold', color: '#28a745' }}>{stats.cardCount}</div>
                        <div style={{ fontSize: '14px', color: '#6c757d', marginTop: '5px' }}> </div>
                    </div>
                    <div style={{ padding: '15px', backgroundColor: '#fff', borderRadius: '4px', textAlign: 'center' }}>
                        <div style={{ fontSize: '24px', fontWeight: 'bold', color: '#ffc107' }}>{stats.todayReviews}</div>
                        <div style={{ fontSize: '14px', color: '#6c757d', marginTop: '5px' }}> </div>
                    </div>
                    <div style={{ padding: '15px', backgroundColor: '#fff', borderRadius: '4px', textAlign: 'center' }}>
                        <div style={{ fontSize: '24px', fontWeight: 'bold', color: '#dc3545' }}>{stats.dueCards}</div>
                        <div style={{ fontSize: '14px', color: '#6c757d', marginTop: '5px' }}> </div>
                    </div>
                </div>

                {/*     */}
                <ProgressChart weeklyData={weeklyData} />
            </div>
        );
    }
}

export default Statistics;
